.header {
    padding: 20px;
    border-bottom: 1px solid #c4c4c4;
    text-align: center;
}

.Card {
    margin-bottom: 15px;
}
.Card:last-child {
    margin-bottom: 0;
}

.table .row {
    border-bottom: 1px solid #c4c4c4;
    display: flex;
    justify-content:space-between;
}

.table .row:last-child {
    border-bottom: 0;
}

.table .row .cell {
    padding: 15px 15px;
    display: inline-block;
}

.table .row .cell {
    border-right: 1px solid #c4c4c4;
}

.table .row .cell:last-child {
    border-right: 0;
}

.index, .grade {
    width: 10%;
    height: 2em;
    line-height: 2em;
    text-align: center;
}

.grade {
    opacity: 0.8;
}

.index {
    color: gray;
}

.hw {
    width: 80%;
    text-align: left;
}

.subject, .small-hw {
    width: 100%;
    font-size: 15px;
}

.small-hw {
    font-size: 13px;
    color: gray;
}

.good {
    color: #4bb34b;
}

.bad {
    color: indianred;
}

.hidden {
    display: none;
}

.visible {
    display: block;
}
.add {
    height: 2em;
    line-height: 2em;
}